<template>
	<div class="hello">
		<h1>一键生成 视频导航素材.mp4</h1>
		<h3 v-if='waitTime<=0'>冬灰条制作</h3>
		<div v-else>
			<h3 style="color:blue">
				<span>正在录制生成素材，倒计时 :</span>
				<span v-text="waitTime"></span>
				<span>秒</span>
			</h3>
		</div>
		<canvas id="myCanvas" :width='vedioWidth' :height='navHeight'></canvas>
		<!-- 一个环节 -->
		<div v-for="(part,index) in arr" :key="index" class="onePart">
			<h3 v-text="index+1"></h3>
			<el-input @input="changeText(index)" v-model="part.name" class="partName" placeholder='请输入环节名称  如 : 开头介绍'></el-input>
			<!-- 如果是第一个环节，那么最低就是0 -->
			<!-- 如果是第二个环节以上，那么最低就是上一个环节的end的minites -->
			<el-input-number
				v-model="part.end.minites"
				controls-position="right"
				:min="getMinitesMin(index)"
				@change="changeNumber(index)"
			>
			</el-input-number>
			<div style="margin:0 21px;">分钟</div> 
			<!-- 如果是第一个环节，那么最低就是0 -->
			<!-- 如果是第二个环节以上，如果和上一个环节的end的minites相等，那么最低就是上一个环节的seconds -->
			<!-- 如果是第二个环节以上，如果和上一个环节的end的minites不等，那么最低就是0 -->
			<el-input-number
				v-model="part.end.seconds"
				controls-position="right"
				:min="getSecondsMin(index)"
				:max="60"
				@change="changeNumber(index)"
			>
			</el-input-number>
			<div style="margin:0 21px;">秒</div> 
			<!-- 删除按钮 -->
			<i v-if="index>0&&waitTime<=0" 
			   class="el-icon-delete-solid deleteOnePart"
			   @click="deleteOnePart(index)"
			></i>
			<div v-else style="width:21px"></div>
		</div>
		<el-button @click="addOnePart" :disabled='waitTime>0'>增加一个环节</el-button>
		<div style="margin-top:30px">
			<el-button :disabled='waitTime>0' type='success' @click="getNavVedio">一键生成视频导航.mp4</el-button>
		</div>
	</div>
</template>

<script>
export default {
	name: "HelloWorld",
	mounted(){
		window.resizeTo(3000, 600);
		 // 获取画布
        let canvas = document.getElementById("myCanvas");
        // 创建上下文
		this.ctx= canvas.getContext("2d");
		
        // 画一个背景
        this.ctx.fillStyle = "black";
        this.ctx.fillRect(0,0,this.vedioWidth,this.navHeight);
        // 画一个边框
        this.ctx.lineWidth = this.lineWidth*2;
        this.ctx.strokeStyle = 'blue';
		this.ctx.strokeRect(0,0,this.vedioWidth,this.navHeight);
		this.drawText(this.lineWidth,0);
	},
	data() {
		return {
			num: 0,
			ctx:null,
			arr:[
				{
					name:'开头介绍',
					end:{
						minites:0,
						seconds:30
					}
				},
				{
					name:'中间展示',
					end:{
						minites:1,
						seconds:30
					}
				},
				{
					name:'结尾部分',
					end:{
						minites:2,
						seconds:0
					}
				},
			],
			vedioWidth:1920,
			navHeight:54,
			lineWidth:3,
			waitTime:0,
			mediaRecord:null,
			videoUrl:null,
			chunks:null,
		};
	},
	computed:{
		allSeconds(){
			return this.arr[this.arr.length-1].end.minites*60+this.arr[this.arr.length-1].end.seconds
		},
	},
	methods:{
		// 修改环节名称
		changeText(index){
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		// 清空画布
		clearCanvas(){
			// 清空画布
			this.ctx.clearRect(0,0,this.vedioWidth,this.navHeight);
			// 不知道为什么，只有运行下面这两句才能清理画布的线
			let canvas = document.getElementById("myCanvas");
			canvas.width = canvas.width
			this.ctx.fillStyle = "black";
			this.ctx.fillRect(0,0,this.vedioWidth,this.navHeight);
			// 画一个边框
			this.ctx.lineWidth = this.lineWidth*2;
			this.ctx.strokeStyle = 'blue';
			this.ctx.strokeRect(0,0,this.vedioWidth,this.navHeight);
		},
		// 给进度条填入文字
		drawText(startPosition,index){
			// 获取本环节的时长
            let partSeconds = this.getPartSeconds(index);
            // 获得本环节该占多少像素
            let partLength = (partSeconds * this.vedioWidth)/this.allSeconds;
            // 获得结束时的位置
			let endPosition = startPosition+partLength+3;
			this.ctx.font = '30px bold';
            this.ctx.textAlign = 'center';
			this.ctx.textBaseline = 'middle';
			
			this.ctx.fillStyle =this.arr[index].name==''?'red':'white';
			
            this.ctx.fillText(this.arr[index].name==""?'环节名称':this.arr[index].name, startPosition+(partLength/2), this.navHeight/2);
            this.ctx.moveTo(endPosition,this.lineWidth);
            this.ctx.lineTo(endPosition,this.navHeight-this.lineWidth);
            this.ctx.lineWidth = this.lineWidth;
            this.ctx.strokeStyle = 'blue';
			this.ctx.stroke();
            if(index<this.arr.length-1) this.drawText(endPosition,index+1);
		},
		// 获取分钟的最小值
		getMinitesMin(index){
			if(index==0) return 0;
			return this.arr[index-1].end.minites;
		},
		// 获取秒的最小值
		getSecondsMin(index){
			if(index==0) return 0;
			if(this.arr[index-1].end.minites==this.arr[index].end.minites) return this.arr[index-1].end.seconds;
			return 0
		},
		// 获取时间的时长，单位为秒
		getTimeLength(time){
			return time.minites*60+time.seconds;
		},
		// 获取环节时间长度，单位为秒
		getPartSeconds(index){
			if(index==0) return this.getTimeLength(this.arr[index].end);
			return this.getTimeLength(this.arr[index].end)-this.getTimeLength(this.arr[index-1].end);
		},
		// 增加一个环节
		addOnePart(){
			//增加时，复制上一个环节的时间
			let end = JSON.parse(JSON.stringify(this.arr[this.arr.length-1].end));
			this.arr.push({
				name:'',
				end
			});
			
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		// 当删除一个环节时
		deleteOnePart(index){
			this.arr.splice(index,1)
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		// 当修改时间时
		changeNumber(index){
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
			/*
				如果修改的不是最后一项
				如果这个环节的end比下一个环节的end大
				那就直接修改下一个环节的end
			*/
			if(index!=this.arr.length-1
			&&
			this.getTimeLength(this.arr[index].end)>this.getTimeLength(this.arr[index+1].end)){
				this.arr[index+1].end = JSON.parse(JSON.stringify(this.arr[index].end));
				this.changeNumber(index+1);
			}
		},
		download(){
			this.mediaRecord.stop() // 结束录屏
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
			const videoBlob = new Blob(this.chunks, { 'type': 'video/mp4' })   // 创建视频文件
			this.videoUrl = window.URL.createObjectURL(videoBlob)    // 创建对象链接
			let a = document.createElement('a')
			a.href = this.videoUrl
			a.download = '冬灰条帮你自动生成的进度条.mp4'
			a.style.display = 'none'
			document.body.appendChild(a)
			a.click()
		},
		//当生成素材时
		getNavVedio(){
			// 开头这部分是直接从网上抄的代码
			this.chunks = new Set() // 先准备个容器保存canvas来的图像流
			let createRecord = ()=>{
				const mediaStream = document.querySelector('#myCanvas').captureStream(48) 
				// 获取画布canvasElement并设置帧频率(FPS)
				this.mediaRecord = new MediaRecorder(mediaStream, {   
					// 核心API，可以录制canvas, audio, video代码下方提供文档链接
					videoBitsPerSecond: 8500000
				})
				this.mediaRecord.ondataavailable = (e) => { // 接收数据
					this.chunks.add(e.data)
				}
				this.mediaRecord.start(20)  // 开始录屏,参数为抓取间隔100毫秒
			};
			// 以下是自己写的代码
			// 总时长
			let timeMax = this.allSeconds*1000;
			// 多少毫秒变化一次
			let eachStepTime = 1000
			// 每次改变进度条的像素
			let step = (this.vedioWidth-this.lineWidth)/(timeMax/eachStepTime);
			
            // 改变进度条的次数
			let process = 0;
	
			let height = this.navHeight;
			let videoWidth = this.vedioWidth;
			// 因为发现setInterval有性能问题，所以换成了setTimeout递归
			let timer = (width)=>{
				setTimeout(()=>{
					// 先绘制进度
					this.ctx.fillStyle = 'blue';
					this.ctx.fillRect(0,0,width,54);
					// 然后把分割线与文字画到进度条上
					this.drawText(3,0);
					// 改变次数增加，等待下一次绘制进度
					process++;
					if(width>videoWidth){
						this.download();
					}else{
						timer(width+step);
						this.waitTime--;
					}
				},1000)
			}
			this.waitTime = this.allSeconds;
			createRecord();
			timer(step);
		}, 
	}
};
</script>

<style scoped>
#myCanvas{
	margin-bottom: 30px;
}
.onePart{
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 30px;
}
.partName{
	width: 240px;
	margin:0 30px;
}
.deleteOnePart{
	cursor: pointer;
	color: red;
	font-size: 21px;
}
</style>
